import React from 'react'

const show_list = [
    {id:1, show_name:"Everybody Loves Raymond"},
    {id:2, show_name: "Parenthood"}, 
    {id:3, show_name: "Ice Age"},
]

const Show = ({show_name}) => <div> Check out Ray in {show_name}</div>


export default function Body(){
    return (
        <div className='Body'>
            {show_list.map(show => <Show key={show.id} show_name={show.show_name}/>)}
        </div>
            
    );
}
